/* eslint @typescript-eslint/no-unsafe-member-access: 0 */
/* eslint no-alert: 0 */
/* eslint @typescript-eslint/no-unsafe-call: 0 */
// TODO event.target„ÇíÂºïÊï∞„Å´ÊåáÂÆö„Åß„Åç„Å™„ÅÑÊñπÊ≥ï„ÇíË™øÊüª„Åô„Çã„ÄÇ‰∏ÄÊó¶ÂïèÈ°å„ÅåÂá∫„Å™„ÅÑ„Åì„Å®„ÅØÁ¢∫Ë™çÊ∏à„Åø„Å™„ÅÆ„Åßany„ÅßÂõûÈÅø„ÄÇ
import { useState, VFC } from 'react';
import { Item, Icon, Button } from 'semantic-ui-react';
import { Container, Draggable, DropResult } from 'react-smooth-dnd';
import arrayMove from 'array-move';
import { useDispatch, useSelector } from 'react-redux';
import ResetButton from '../atoms/ResetButton';
import AddButton from '../atoms/AddButton';
import InputForm from '../atoms/InputForm';
import Introduction from '../atoms/Intoduction';
// eslint-disable-next-line import/no-cycle
import { TaskListState } from '../../reducer';

/* ---------„Çø„Çπ„ÇØ„É™„Çπ„ÉàÊú¨‰Ωì„ÅÆ„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà--------- */

/* ----------„Çø„Çπ„ÇØ„ÅÆÂûãÂÆöÁæ©---------- */
export type Task = {
  id: number;
  title: string;
  mode: string;
};

type Props = {
  add?: (id: number, taskName: string) => void;
  del?: (index: number) => void;
  edit?: () => void;
  refresh?: (list: Task[]) => void;
};
/* -------------------------------- */

const TaskList: VFC<Props> = ({
  add = () => undefined,
  del = () => undefined,
  edit = () => undefined,
  refresh = () => undefined,
}) => {
  /* --------Áä∂ÊÖã(state)„ÅÆÂÆöÁæ©------------- */

  // „Çø„Çπ„ÇØÂêç
  const [taskName, setTaskName] = useState<string>('');
  // „Çø„Çπ„ÇØ„ÅÆid
  const [idCnt, setIdCnt] = useState(1);
  // ËøΩÂä†„Éú„Çø„É≥„ÅÆÊ¥ªÊÄßÂà∂Âæ°
  const [disabled, setDisabled] = useState(true);

  // „Çø„Çπ„ÇØ„ÇíÁ∑®ÈõÜÂèØËÉΩ„Å´„Åô„Çã„Åü„ÇÅ„ÅÆ„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà
  const [editTask, setEditTask] = useState<Task>({
    id: 0,
    title: '',
    mode: '',
  });

  /* ------------Store,Reducer„Å´Èñ¢„Åô„Çã------------ */
  // Store„Å´Ê†ºÁ¥ç„Åó„Å¶„ÅÇ„Çã„Çø„Çπ„ÇØ„É™„Çπ„Éà„ÅÆÊú¨‰Ωì
  const taskListState = useSelector<TaskListState, Task[]>((state) => state);

  // dispatcher
  const dispatch = useDispatch();

  /* ------------„Çø„Çπ„ÇØ„ÅÆËøΩÂä†„Å´Èñ¢„Åô„ÇãÂá¶ÁêÜ------------ */
  // ---„Çø„Çπ„ÇØ„ÅÆËøΩÂä†
  const addTask = () => {
    const newIdCnt = idCnt + 1;
    setIdCnt(newIdCnt);
    // call reducer
    dispatch(add(newIdCnt, taskName));

    // ÂÖ•Âäõ„Åï„Çå„ÅüÂÄ§„Çí„ÇØ„É™„Ç¢„Éº„Åô„Çã
    setTaskName('');
    setDisabled(true);
  };

  /* ------------ÂÖ•Âäõ„Éï„Ç©„Éº„É†„Å´Èñ¢„Åô„ÇãÂá¶ÁêÜ------------ */
  // ÂÖ•Âäõ„Éï„Ç©„Éº„É†„ÅÆÂÖ•Âäõ„ÇíÂèó‰ªò„Åë„Çã„Åü„ÇÅ„ÅÆÂá¶ÁêÜ
  const handleValue = (target: HTMLInputElement) => {
    // 35ÊñáÂ≠ó‰ª•‰∏ä„ÅÆÂÖ•Âäõ„ÅØ„Çø„Çπ„ÇØ„Åã„Çâ„ÅØ„ÅøÂá∫„Å¶„Åó„Åæ„ÅÜ„ÅÆ„ÅßÂèó„Åë‰ªò„Åë„Å™„ÅÑ
    if (target.value.length > 35) {
      return;
    }
    setTaskName(target !== null ? target.value : '');
    setDisabled(!(target.value.length > 0));
  };

  /* ------------„Çø„Çπ„ÇØ„ÅÆÂâäÈô§„Å´Èñ¢„Åô„ÇãÂá¶ÁêÜ------------ */

  // ---ÂÖ®„Å¶„ÅÆ„Çø„Çπ„ÇØ„ÅÆ„É™„Çª„ÉÉ„Éà
  const reset = () => {
    const confirmAnswer = window.confirm('Do you wanna delete All Task ?');
    // „Ç≠„É£„É≥„Çª„É´„Åï„Çå„ÅüÂ†¥Âêà„ÅØ„É™„Çª„ÉÉ„Éà„Çí‰∏≠Êñ≠
    if (!confirmAnswer) {
      return;
    }

    // setTask([]);
    dispatch(refresh([]));
  };

  /* ------„Çø„Çπ„ÇØ„Çí„Éâ„É©„ÉÉ„Ç∞&„Éâ„É≠„ÉÉ„Éó„Åß„ÇΩ„Éº„ÉàÂèØËÉΩ„Å´„Åô„Çã„Åü„ÇÅ„ÅÆÈñ¢Êï∞------ */

  const onDrop = (dropResult: DropResult) => {
    // `DropResult` „ÅßÂûãÂÆöÁæ©
    const { removedIndex, addedIndex } = dropResult;
    // setTask((tasksArray) =>
    //   arrayMove(tasksArray, removedIndex || 0, addedIndex || 0),

    let newTaskListState = [...taskListState];
    newTaskListState = arrayMove(
      newTaskListState,
      removedIndex || 0,
      addedIndex || 0,
    );
    dispatch(refresh(newTaskListState));
  };

  /* ------------„Çø„Çπ„ÇØ„ÅÆUpdate„Å´Èñ¢„Åô„ÇãÂá¶ÁêÜ------------ */

  // „Çø„Çπ„ÇØ„ÅÆÁ∑®ÈõÜ‰∏≠„Å´‰ªñ„ÅÆ„Ç≥„É≥„Éà„É≠„Éº„É´„ÇíÊìç‰Ωú„Åß„Åç„Å™„ÅÑ„Çà„ÅÜ„Å´„Åô„ÇãÂá¶ÁêÜ
  const toggleInvalidControlTarget = () => {
    const targetList = document.getElementsByClassName('controlItem');
    const tempTargetList = Array.prototype.slice.call(targetList);

    tempTargetList.forEach((target) =>
      // eslint-disable-next-line @typescript-eslint/no-unsafe-return
      target.classList.toggle('event-invalid'),
    );
  };

  // Á∑®ÈõÜÁî®„Çø„Çπ„ÇØ„Å´ÂºïÊï∞„ÅÆ„Çø„Çπ„ÇØ„Çí„Çª„ÉÉ„Éà„Åó„Å¶Á∑®ÈõÜÂèØËÉΩ„Å´„Åô„ÇãÂá¶ÁêÜ
  const editTaskTitle = (task: Task) => {
    if (taskListState.filter((t) => t.mode === 'edit').length > 0) {
      return;
    }
    const tempTask = task;
    tempTask.mode = 'edit';

    // „Çø„Çπ„ÇØ„ÅÆ„É™„Çπ„Éà„ÇíÊõ¥Êñ∞„Åó„Å¶ÂÜç„É¨„É≥„ÉÄ„É™„É≥„Ç∞„Åï„Åõ„Çã
    // call reducer
    dispatch(edit());
    setEditTask(task);
    toggleInvalidControlTarget();
  };

  // „Çø„Çπ„ÇØ„ÅÆÂÜÖÂÆπ„ÇíÁ∑®ÈõÜ„Åô„ÇãÂá¶ÁêÜ
  const handleTaskTitle = (target: HTMLInputElement) => {
    const newEditTask = { ...editTask };
    newEditTask.title = target.value;
    setEditTask(newEditTask);
  };

  // „Çø„Çπ„ÇØ„ÅÆÁ∑®ÈõÜ„ÇíÂÆå‰∫Ü„Åô„ÇãÂá¶ÁêÜ
  const onEnterForCompleteEdit = (key: string) => {
    if (key === 'Enter') {
      // „Çø„Çπ„ÇØ„É™„Çπ„Éà„ÅÆÈÖçÂàó„ÇíÊúÄÊñ∞Âåñ„Åô„Çã
      const foundEditTask = taskListState.find(
        (task: Task) => task.mode === 'edit',
      );
      if (foundEditTask === undefined) {
        return;
      }
      const newTasks = [...taskListState];
      editTask.mode = 'display';
      newTasks[newTasks.indexOf(foundEditTask)] = editTask;
      // call reducer
      dispatch(refresh(newTasks));
      toggleInvalidControlTarget();
    }
  };

  /* ------------------------------------------------------ */

  return (
    <>
      <div className="task-list-container">
        <InputForm
          taskName={taskName}
          onChangeHandler={(e) => handleValue(e.target)}
        />
        <AddButton addTask={addTask} disabled={disabled} />
        <ResetButton reset={reset} />

        <Item.Group>
          <Container
            drag-handle-selector=".drag-handle-selector"
            lockAxis="y"
            onDrop={onDrop}
          >
            {taskListState.map((task, index) => (
              <Draggable key={task.id}>
                <Item key={task.id} className="task">
                  <Item.Content>
                    <Icon className="task-icon" name="certificate" size="big" />
                    <span className="task-number">{index + 1}</span>
                    {task.mode === 'edit' ? (
                      <input
                        className="edit-input-form"
                        type="text"
                        value={editTask.title}
                        onChange={(e) => handleTaskTitle(e.target)}
                        onKeyPress={(e) => onEnterForCompleteEdit(e.key)}
                      />
                    ) : (
                      <Item.Header
                        className="task-header"
                        onDoubleClick={() => editTaskTitle(task)}
                      >
                        {task.title}
                      </Item.Header>
                    )}
                    <Icon
                      className="drag-handle-selector controlItem"
                      name="sort"
                      size="big"
                    />
                    <Button
                      className="delete controlItem"
                      color="yellow"
                      size="mini"
                      onClick={() => dispatch(del(index))}
                    >
                      √ó
                    </Button>
                  </Item.Content>
                </Item>
              </Draggable>
            ))}
          </Container>
        </Item.Group>
        <Introduction />
      </div>
    </>
  );
};

export default TaskList;
